.Imagebox {
  position: relative;
  display: inline-block;
  vertical-align: top;
  margin: 0 0 10px 10px;
  border-radius: 4px;
  box-sizing: border-box;
  overflow: hidden;
  border: 1px solid var(--border_color);
  .Upload {
    width: 100%;
    height: 100%;
    :global(.ant-upload.ant-upload-select-picture-card) {
      width: 100%;
      height: 100%;
      margin-right: 0;
      margin-bottom: 0;
      background-color: var(--bg_color);
      >span {
        color: var(--color);
      }
    }
    .addIcon {
      display: block;
      margin-bottom: 10px;
      font-size: 52px;
    }
    .progress {
      font-size: 12px;
      .name {
        font-weight: bold;
        font-size: 14px;
        margin-bottom: 8px;
      }
      :global(.ant-progress-text) {
        display: none;
      }
    }
  }
  .imageLoading {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid var(--border_color);
    box-sizing: border-box;
    background-color: rgba(0,0,0,0.25);
  }
  .shower {
    width: 100%;
    height: 100%;
    font-size: 14px;
    color: var(--color);
  }
  .Icons {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    color: var(--color);
    font-size: 2rem;
    .iconBoxIcon {
      margin: 0 5px;
      cursor: pointer;
      &:hover {
        color: var(--color_active);
      }
    }
    .name, .size, .time {
      width: 100%;
      text-align: center;
      font-size: 16px;
    }
  }
}